@use 'base/variables' as v;
@use 'base/mixins' as m;

.banner__portfolio{
    background-image: url('../img/3.png'), linear-gradient(to right, rgba(30, 75, 115, 1), rgba(255, 255, 255, 0));
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 40rem;
    position: relative;
    z-index: 1;

    display: flex;
    flex-direction: column;
    position: relative;

}


.portfolio__layer{
    background-color: v.$negro;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: .3;
    z-index: -9999;
}


.banner__text{
    color: v.$blanco;
    text-align: center;
}

.banner__title{
    margin: 4rem 0;
    font-size: 3.2rem;
    font-weight: bold;

    @include m.tablet{
        font-size: 4.5rem;
    }

}

.banner__par{
    font-size: 1.5rem;
    width: 90%;
    text-align: left;
    max-width: 120rem;
    margin: 0 auto;

    @include m.tablet{
        font-size: 2rem;
    }
}

.portfolio__videos{
    margin-top: -25rem;
    z-index: 999;
    position: relative;
}

.video{
    width: 100%;
    height: 22rem;

    @include m.tablet{
        height: 45rem;
    }

    @include m.desktop{
        width: 60%;
        height: 55rem;
        display: block;
        margin: 0 auto;
    }
}

.portfolio__head{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5rem 0 5rem 0;
    gap: 1rem;

    img{
        width: 5rem;
    }
    
}

.portfolio__title{
    color: v.$secundario;
    font-weight: bold;
    font-size: 4rem;

    @include m.desktop { 
        font-size: 5rem;
    }
}

.portfolio__subtitle{
    margin: 7rem;
    color: v.$secundario;
    font-weight: bold;
    font-size: 3.5rem;
    text-align: center;
}

.portfolio__body{
    display: flex;
    flex-direction: column;
    gap: 5rem;

    @include m.desktop { 
        flex-direction: row;
    }
}

.portfolio__grid{
    display: grid;
    grid-template-columns: 1fr;
    gap: 5rem;
    width: 90%;
    margin: 0 auto;

    @include m.tablet { 
        width: 90%;
        grid-template-columns: repeat(2, 1fr);
    }

    @include m.desktop { 
        grid-template-columns: repeat(3, 1fr);
    }
}

.portfolio__card{
    border: 1px solid v.$grisOscuro;
    padding: 2rem;
    border-radius: 1rem;
    -webkit-box-shadow: 0px -2px 31px -6px rgba(0,0,0,0.38);
    -moz-box-shadow: 0px -2px 31px -6px rgba(0,0,0,0.38);
    box-shadow: 0px -2px 31px -6px rgba(0,0,0,0.38);

    display: grid;
    justify-items: center;
    align-items: center;
}

.porfolio__card-img{
    overflow: hidden;
    border-radius: 2rem;

    img{
        width: 28rem;
        height: 28rem;
        object-fit: cover;
        margin: 0 auto;
        border-radius: 2rem;
        transition: transform .3s ease-in-out;
        cursor: pointer;

        &:hover{
            transform: scale(1.1);
        }
    }
}

#myImg {
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}
  
#myImg:hover {opacity: 0.7;}
  
.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}
  
  /* Modal Content (image) */
.modal-content {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
}
  
/* Caption of Modal Image */
#caption {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
    text-align: center;
    color: #ccc;
    padding: 10px 0;
    height: 150px;
}
  
/* Add Animation */
.modal-content, #caption {  
    -webkit-animation-name: zoom;
    -webkit-animation-duration: 0.6s;
    animation-name: zoom;
    animation-duration: 0.6s;
}
  
@-webkit-keyframes zoom {
    from {-webkit-transform:scale(0)} 
    to {-webkit-transform:scale(1)}
}
  
@keyframes zoom {
    from {transform:scale(0)} 
    to {transform:scale(1)}
}
  
/* The Close Button */
.close {
    position: absolute;
    top: 15px;
    right: 35px;
    color: #f1f1f1;
    font-size: 40px;
    font-weight: bold;
    transition: 0.3s;
}
  
.close:hover,
.close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
}
  
/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px){
    .modal-content {
      width: 100%;
    }
}

video{
    width: 100%;
    display: block;
}


.portfolio__card-title{
    margin: 1rem auto;
    color: v.$terciario;
    font-size: 2.5rem;
    text-align: center;
    font-weight: bold;
}

.portfolio__card-text{
    margin: 1rem;
    text-align: left;
}

.portfolio__card-tags{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1rem;
    margin: 1rem;

    .tags{
        padding: 0.5rem 1rem;
        border: 2px solid v.$secundario;
        border-radius: 2rem;

        &:hover{
            cursor: pointer;
            background-color: v.$secundario;
            color: v.$blanco;
        }
    }
}

.portfolio__button{
    @include m.boton(v.$secundario, v.$blanco);
    display: block;
    width: 100%;
    cursor: pointer;
    border-radius: 2rem;

    -webkit-box-shadow: 14px 13px 14px -8px rgba(0,0,0,0.28);
    -moz-box-shadow: 14px 13px 14px -8px rgba(0,0,0,0.28);
    box-shadow: 14px 13px 14px -8px rgba(0,0,0,0.28);

    &:hover{
        color: v.$blanco;
    }

}

.more{
    background-color: v.$secundario;
    text-align: center;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
    padding: 1.8rem 2.2rem;
    color: v.$blanco;
    font-weight: bold;
    margin-top: 7rem;
    width: 6rem;
    cursor: pointer;
    font-size: 2.2rem;

    &:hover{
        background-color: darken(v.$secundario, 7%);
        color: v.$blanco;
    }
}